body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.youtube-icons{
  background-color:rgb(160, 159, 159);
  max-width: 80px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 5px;
}

.youtube-icons .bar{
  font-size: 40px;
  margin:0px;
  padding-top: 10px;
}

.youtube-icons h1{
  font-size: 30px;
  margin: 50px 0px;
  padding: 0px 0px 25px 0px;
}

.youtube-icons h1:hover{
  color:white;
}

.youtube-template{
  display:flex;
}

.youtube-top{
  width:100%;
}

.topic{
  font-size: 40px;
  margin:0px;
  background-color: rgb(160, 159, 159);
  padding:10px;
}

.topic input{
  height: 20px;
  width:25%;
  padding:10px;
  margin:0px 20px 10px 250px;
  border:none;
  border-radius: 8px;
}

.contents{
  padding:30px;
  display:grid;
  grid-template-columns: repeat(3,1fr);
  column-gap: 20px;
}

.grid-content img:hover{
  height:210px;
  width:380px;
}